.payment-container,.flex-box{display:flex;flex-direction:column;align-items:center}.chosen{height:500px;width:500px;display:flex;justify-content:center}.Checkout{margin:0 auto;max-width:800px;box-sizing:border-box;padding:0 5px}.pay-btn{white-space:nowrap;border:0;outline:0;height:40px;line-height:40px;padding:0 14px;color:#fff;border-radius:4px;font-size:15px;font-weight:600;text-transform:uppercase;letter-spacing:0.025em;background-color:#ffa500;text-decoration:none;transition:all 150ms ease}.price{height:100px;display:flex;justify-content:space-around}.radiobtn{display:flex;justify-content:center;align-items:center;border-radius:30px;margin:1em;padding:1.5em 1em;padding-left:0;font-size:20px;font-weight:bold;width:160px;letter-spacing:0.03em}input[type=radio]{-webkit-appearance:none;appearance:none;border-radius:50%;width:16px;height:16px;margin-right:15px;border:2px solid #ffa500;transition:0.2s all linear}input[type=radio]:checked{border:6px solid black;outline:unset !important}.StripeElement{display:block;margin:10px 0 20px 0;max-width:500px;padding:10px 14px;box-shadow:rgba(50,50,93,0.14902) 0px 1px 3px,rgba(0,0,0,0.0196078) 0px 1px 0px;border-radius:4px;background:white}.StripeElement--focus{box-shadow:rgba(50,50,93,0.109804) 0px 4px 6px,rgba(0,0,0,0.0784314) 0px 1px 3px;transition:all 150ms ease}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    background: #fff;
}

.wrapper {
    width: 100%;
    padding: 50px 50px;
}

a {
    color: #000;
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
    margin-bottom: 0.5em;
}

h1 {
    margin-bottom: 20px;
    font-size: 4em;
}

h2 {
    font-size: 3em;
}

h3 {
    font-size: 2em;
}

p {
    opacity: 0.7;
    line-height: 1.5em;
}

.city__grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-column-gap: 50px;
    grid-row-gap: 50px;
}

.search input {
    width: 100%;
    padding: 10px 15px;
    border-radius: 4px;
    outline: none;
    font-size: 18px;
    border: 1px solid #a7a7a7;
    background: #FCFBFE;
}

.input__search {
    margin-bottom: 50px;
}

.container__main {
    max-width: 95vw;
    margin-left: 5vw;
}

.header__top {
    font-size: 2em;
}
.hero__outer-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 0px;
}

.data-map__wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
}

.data__wrapper {
    width: 44vw;
    padding: 25px;
}

.map__wrapper {
    max-width: 100%;
    position: fixed;
    top: 0;
    right: 0;
}

.data__inner-wrapper {
    margin-top: 50px;
}

.data__table {
    width: 100%;
}

.data__table tr th {
    text-align: left;
    padding-bottom: 20px;
}

.data__table tr td {
    padding: 20px 0px;
}

.data__table tr:nth-child(even) {
    background-color: #F7F8F8;
}
.leaflet-container {
    width: 50vw;
    height: 100vh;
}
.city__outer-wrapper {
    background: #fff;
    transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1);
    overflow: hidden;
    border: 1px solid #E4E4E6;
}

.city__outer-wrapper:hover { 
    box-shadow: 0px 20px 20px 0px rgba(0,0,0,0.07);
    cursor: pointer;
}

.city__img-wrapper {
    object-fit: cover;
}

.city__img-wrapper img {
    width: 100%;
}

.city__content-wrapper {
    padding: 20px;
}

.city__bottom-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 25px;
}

.city__stats-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
}

.city__stats-item {
    display: flex;
    align-items: center;
}

.city__icon {
    max-width: 16px;
    margin-right: 5px;
}
