.nav__outer-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 30px 50px;
    background: #fff;
    border-bottom: 1px solid #E4E4E6;
}

.nav__brand {
    font-weight: 500;
}

.nav__link {
    margin-left: 45px;
    text-decoration: none;
    font-weight: 500;
}

.active {
    color: #1E69D1;
}
.nav-left__outer-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 5vw;
    background: #1D1E22;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #fff;
    align-items: center;
    padding: 25px 0px;
}

.nav-left__menu-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;
}

.nav-left__menu-item {
    padding: 35px 0px;
    width: 100%;
}

.menu-item__active {
    background: #333842;
}

.nav-left__outer-wrapper a {
    color: #fff
}

.nav-left__menu-item-icon {
    height: 20px;
}
@font-face {
    font-family: 'Moderat Regular';
    src: local('Moderat Regular'), url(../../static/media/Moderat-Extended-Regular.85beec7d.woff2) format('woff2');
}

.home__container {
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding: 0px 20px;
}

.home-hero__outer-wrapper {
    background: #1D1E22;
    color: #fff;
}

.hero__brand {
    display: flex;
}

.hero__brand img {
    margin-right: 7px;
    max-width: 15px;
}

.home-hero__nav-wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding-top: 35px;
}

.home-hero__nav-wrapper a {
    color: #fff;
}

.home-hero__nav-center-wrapper a {
    margin: 0px 35px;
}

.home-hero__content-outer-wrapper {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 125px 0px;
}

.home-hero__content-inner-wrapper {
    max-width: 60ch;
}

.home-hero__button {
    margin-top: 30px;
}

.home-hero__img {
    margin-bottom: 125px;
    max-width: 100%;
}

.home-content__outer-wrapper {
    padding: 125px 0px;
}

.home-content__content-outer-wrapper {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 125px;
}

.home-content__content-inner-wrapper {
    max-width: 50ch;
}

.home-content__grid-wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-column-gap: 50px;
    grid-row-gap: 50px;
}

.home-content__grid-item {
    aspect-ratio: 1;
}

.home-grid__img {
    max-width: 100%;
}

.h3__grid {
    margin-top: 20px;
}

.home-content__testimonial-wrapper {
    background: #1D1E22;
    color: #fff;
    padding: 150px 0px;
}

.home-content__testimonial-outer-wrapper {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-content__testimonial-inner-wrapper {
    max-width: 50ch;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.p__testimonial {
    font-size: 1.5em;
    opacity: 1;
}

.home-footer__inner-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 125px 0px 50px 0px;
}

.author__wrapper {
    display: flex;
    flex-direction: row;
    text-align: left;
    align-items: center;
    margin-top: 30px;
}

.author__wrapper img {
    margin-right: 15px;
    width: 40px;
}

.home-nav__link {
    transition: opacity 0.2s ease-in-out;
    opacity: 1;
}

.home-nav__link:hover {
    opacity: 0.5;
}

@media (max-width: 768px) {
    .home__container {
        width: 100%;
    }

    .home-hero__nav-center-wrapper {
        display: none;
    }

    .home-hero__nav-right-wrapper {
        display: none;
    }
    
    .home-content__grid-wrapper{
        grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
        grid-column-gap: 0px;
        grid-row-gap: 0px;
    }
}

