.payment-container,
.flex-box {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.chosen {
    height: 500px;
    width: 500px;
    display: flex;
    justify-content: center;
}

.Checkout {
  margin: 0 auto;
  max-width: 800px;
  box-sizing: border-box;
  padding: 0 5px;
}

.pay-btn {
    white-space: nowrap;
    border: 0;
    outline: 0;

    height: 40px;
    line-height: 40px;
    padding: 0 14px;
    color: #fff;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    background-color: #ffa500;
    text-decoration: none;
    -webkit-transition: all 150ms ease;
    transition: all 150ms ease;
}

.price {
      height: 100px;
      display: flex;
      justify-content: space-around;
}

.radiobtn {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    margin: 1em;
    padding: 1.5em 1em;
    padding-left: 0;
    font-size: 20px;
    font-weight: bold;
    width: 160px;
    letter-spacing: 0.03em;
}

input[type=radio] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  border-radius: 50%;
  width: 16px;
  height: 16px;
  margin-right: 15px;
  border: 2px solid #ffa500;
  transition: 0.2s all linear;
}

input[type=radio]:checked {
  border: 6px solid black;
  outline: unset !important;
}

.StripeElement {
  display: block;
  margin: 10px 0 20px 0;
  max-width: 500px;
  padding: 10px 14px;
  box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px, rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border-radius: 4px;
  background: white;
}

.StripeElement--focus {
  box-shadow: rgba(50, 50, 93, 0.109804) 0px 4px 6px, rgba(0, 0, 0, 0.0784314) 0px 1px 3px;
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
}
